import React from "react";
import { useSelector } from "react-redux";
import { XAxis, ResponsiveContainer, Bar, BarChart, Cell } from "recharts";

const Barchart = () => {
  const data = useSelector((state) => state.forecasts.barchart);

  return (
    <div
      className="line-chart-wrapper"
      style={{
        width: "100%",
        maxWidth: 990,
        height: "400px",
        margin: "0 auto",
      }}
    >
      <ResponsiveContainer>
        <BarChart width={400} height={400} data={data}>
          <XAxis fontSize={10} interval={0} dataKey="name" />
          <Bar barSize={80} fill="#81d4fa" yAxisId="a" dataKey="temp">
            {data.map((entry, index) => (
              <Cell key={`cell-${index}`} />
            ))}
          </Bar>
        </BarChart>
      </ResponsiveContainer>
    </div>
  );
};

export default Barchart;
