import React, { useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import Carousel from "./Carousel";
import ForcastsListItem from "./ForcastsListItem";
import {
  getGroupedForcasts,
  generateForcastsPerDay,
  removeForcastsPerDay,
} from "../store/forecast";

const Forecasts = () => {
  const [selected, setSelected] = useState("");
  const dispatch = useDispatch();
  const forecasts = useSelector(getGroupedForcasts);
  const units = useSelector((state) => state.forecasts.units);
  const keys = Object.keys(forecasts);

  let content = <div />;

  const showBarchart = (index) => {
    setSelected(index);
    dispatch(generateForcastsPerDay(forecasts[keys[index]], units));
  };

  const sliderChange = () => {
    setSelected(null);
    dispatch(removeForcastsPerDay());
  };

  if (keys) {
    content = keys.map((forecast, index) => (
      <ForcastsListItem
        forecast={forecasts[forecast][0]}
        key={index}
        selected={selected}
        index={index}
        metric={units}
        sliderChange={showBarchart}
      />
    ));
  }

  return <Carousel sliderChange={sliderChange}>{content}</Carousel>;
};

export default Forecasts;
