import { createSlice } from "@reduxjs/toolkit";
import { createSelector } from "reselect";
import { apiCallBegan } from "./api";
import moment from "moment";

const slice = createSlice({
  name: "forecast",
  initialState: {
    list: [],
    barchart: [],
    units: "imperial",
    loading: false,
    lastFetch: null,
  },
  reducers: {
    forcastRequested: (forcasts) => {
      forcasts.loading = true;
    },

    forcastReceived: (forcasts, action) => {
      forcasts.list = action.payload.list;
      forcasts.barchart = [];
      forcasts.loading = false;
      forcasts.lastFetch = Date.now();
    },

    forcastRequestFailed: (forcasts) => {
      forcasts.loading = false;
    },

    updateUnits: (forcasts, action) => {
      forcasts.units = action.payload;
    },

    generateDayForcast: (forcasts, action) => {
      forcasts.barchart = action.payload.datas;
    },

    removeForcastsPerDay: (forcasts) => {
      forcasts.barchart = [];
    },
  },
});

export const {
  forcastReceived,
  forcastRequested,
  forcastRequestFailed,
  generateDayForcast,
  removeForcastsPerDay,
  updateUnits,
} = slice.actions;

export default slice.reducer;

// Action Creators
export const loadForcasts = () => (dispatch, getState) => {
  const { units } = getState().forecasts;

  const url = `/data/2.5/forecast?q=Munich,de&APPID=5ac14a0a4dd5776f32a6e3591786dd6a&cnt=40&units=${units}`;

  // const diffInMinutes = moment().diff(moment(lastFetch), "minutes");

  return dispatch(
    apiCallBegan({
      url,
      onStart: forcastRequested.type,
      onSuccess: forcastReceived.type,
      onError: forcastRequestFailed.type,
    })
  );
};

export const getGroupedForcasts = createSelector(
  (state) => state.forecasts,
  (forecasts) =>
    forecasts.list.reduce((accumulator, currentValue) => {
      const date = moment(currentValue.dt_txt).format("DD");

      accumulator[date] = [...(accumulator[date] || []), currentValue];
      return accumulator;
    }, {})
);

export const generateForcastsPerDay = (forcast, metric) => (dispatch) => {
  const m = metric === "metric" ? "C" : "F";
  const datas = [
    ...forcast.map((daily) => {
      return {
        name: daily.main.temp + m,
        temp: daily.main.temp,
      };
    }),
  ];

  return dispatch(
    generateDayForcast({
      datas,
    })
  );
};
