import React, { Component } from "react";
import Slider from "react-slick";
import IconButton from "@material-ui/core/IconButton";
import NavigateNext from "@material-ui/icons/NavigateNext";
import NavigateBefore from "@material-ui/icons/NavigateBefore";
import { makeStyles } from "@material-ui/core/styles";

const useStyles = makeStyles({
  next: {
    position: "absolute",
    right: 0,
    top: 0,
  },
  slickHide: {
    visibility: "hidden",
  },
});

function SampleNextArrow(props) {
  const { className, onClick } = props;
  const classes = useStyles();

  return (
    <IconButton
      className={`${classes.next} 
      ${className.includes("slick-disabled") ? classes.slickHide : ""}`}
      aria-label="next"
      onClick={onClick}
    >
      <NavigateNext fontSize="large" />
    </IconButton>
  );
}

function SamplePrevArrow(props) {
  const { className, onClick } = props;
  const classes = useStyles();

  return (
    <IconButton
      className={className.includes("slick-disabled") && classes.slickHide}
      aria-label="prev"
      onClick={onClick}
    >
      <NavigateBefore fontSize="large" />
    </IconButton>
  );
}

class Carousel extends Component {
  render() {
    const { sliderChange } = this.props;

    var settings = {
      dots: false,
      infinite: false,
      speed: 500,
      slidesToShow: 3,
      slidesToScroll: 1,
      beforeChange: (index) => sliderChange(index),
      nextArrow: <SampleNextArrow />,
      prevArrow: <SamplePrevArrow />,
      responsive: [
        {
          breakpoint: 900,
          settings: {
            slidesToShow: 2,
            slidesToScroll: 1,
            initialSlide: 0,
            dots: true,
          },
        },
        {
          breakpoint: 767,
          settings: {
            slidesToShow: 1,
            slidesToScroll: 1,
            initialSlide: 0,
            dots: true,
          },
        },
      ],
    };

    return <Slider {...settings}>{this.props.children}</Slider>;
  }
}

export default Carousel;
